'use strict';

const xml = require('../../utils/xml');
const events = require('./events');

class XMLHandler {
    constructor() {}

    _toXML(string) {
        return xml.fromStringOriginal(string);
    }

    /**
     * Handles xml.getAttr method call.
     * @param {Object} params
     * @private
     */
    _getAttr({xmlString, selector, attr, sendResponse}) {
        if (typeof xmlString === 'string') {
            const xmlDoc = this._toXML(xmlString);
            sendResponse({attr: xml.getAttrOriginal(xmlDoc, selector, attr)});
        } else {
            const errObj = {message: 'No serialized xml found.'}
            sendResponse({errObj});
        }
    }

    /**
     * Handles xml.getText method call.
     * @param {Object} params
     * @private
     */
    _getText({xmlString, selector, sendResponse}) {
        if (typeof xmlString === 'string') {
            const xmlDoc = this._toXML(xmlString);
            sendResponse({text: xml.getTextOriginal(xmlDoc, selector)});
        } else {
            const errObj = {message: 'No serialized xml found.'}
            sendResponse({errObj});
        }
    }

    /**
     * Handles xml.selectAll method call.
     * @param {Object} params
     * @private
     */
    _selectAll({selector, parent, sendResponse}) {
        if (typeof parent === 'string') {
            const xmlDoc = this._toXML(parent);
            sendResponse({selection: xml.selectAllOriginal(selector, xmlDoc)});
        } else {
            const errObj = {message: 'No serialized xml found.'}
            sendResponse({errObj});
        }
    }

    handle(req, _sender) {
        switch (req.type) {
            case events.backend.XML_FROM_STRING:
                this._fromString(req.data);
                break;
            case events.backend.XML_GET_ATTR:
                this._getAttr(req.data);
                break;         
            case events.backend.XML_GET_TEXT:
                this._getText(req.data);
                break;
            case events.backend.XML_SELECT_ALL:
                this._selectAll(req.data);
                break;
            default:
                // Cannot handle.
                return;
        }

        // Indicate an async response.
        return true;
    }
};

module.exports = new XMLHandler();



