'use strict';

const colorUtils = require('../../utils/colors');
const events = require('./events');

class ColorsHandler {
    constructor() {}

    /**
     * Handles Filer.calculateDominantColorByUrl method call.
     * @param {Object} params
     * @private
     */
    _calculateDominantColorByUrl({imgUrl, options, sendResponse}) {
        colorUtils.calculateDominantColorByUrlOriginal(imgUrl, options, (color) => {
            sendResponse({color});
        });
    }

    /**
     * Handles Filer.calculateFontColorByUrl method call.
     * @param {Object} params
     * @private
     */
    _calculateFontColorByUrl({imgUrl, sendResponse}) {
        colorUtils.calculateFontColorByUrlOriginal(imgUrl, (color) => {
            sendResponse({color});
        });
    }

    handle(req, _sender) {
        switch (req.type) {
            case events.backend.COLORS_DOMINANT_COLOR_BY_URL:
                this._calculateDominantColorByUrl(req.data);
                break;
            case events.backend.COLORS_FONT_COLOR_BY_URL:
                this._calculateFontColorByUrl(req.data);
                break;     
            default:
                // Cannot handle.
                return;
        }

        // Indicate an async response.
        return true;
    }
};

module.exports = new ColorsHandler();



