/**
 * Colors util proxy override
 */
'use strict';

const events = require('../../modules/offscreen/events');
const offscreenManager = require('../offscreen/offscreen-manager');

exports.override = (colors) => {
    if (typeof colors !== 'object') {
        return;
    }

    // Keep functions that is going to be overrided
    // to call them from the offscreen document.
    colors.calculateDominantColorByUrlOriginal =
        colors.calculateDominantColorByUrl.bind({});
    colors.calculateFontColorByUrlOriginal =
        colors.calculateFontColorByUrl.bind({});

    /**
     * Proxy request to the Colors.calculateDominantColorByUrl method
     * in the offscreen document.
     */
    colors.calculateDominantColorByUrl = (imgUrl, options, successCallback) => {
        const callback = (message) => {
            const {errObj, color} = message;
            if (errObj) {
                throw new Error(errObj.message);
            } else {
                return successCallback(color);
            }
        };

        const msgBody =
            {type: events.backend.COLORS_DOMINANT_COLOR_BY_URL, data: {imgUrl, options}};
        return offscreenManager.sendMessage(
            msgBody, 'Colors calculateDominantColorByUrl timed out', callback);
    };

    /**
     * Proxy request to the Colors.calculateFontColorByUrl method
     * in the offscreen document.
     */
    colors.calculateFontColorByUrl = (imgUrl, successCallback) => {
        const callback = (message) => {
            const {errObj, color} = message;
            if (errObj) {
                throw new Error(errObj.message);
            } else {
                return successCallback(color);
            }
        };

        const msgBody =
            {type: events.backend.COLORS_FONT_COLOR_BY_URL, data: {imgUrl}};
        return offscreenManager.sendMessage(
            msgBody, 'Colors calculateFontColorByUrl timed out', callback);
    }
}



