/**
 * XML util proxy override
 */
'use strict';

const events = require('../../modules/offscreen/events');
const offscreenManager = require('../offscreen/offscreen-manager');

exports.override = (xml) => {
    if (typeof xml !== 'object') {
        return;
    }

    // Keep functions that is going to be overrided
    // to call them from the offscreen document.
    xml.fromStringOriginal = xml.fromString.bind({});
    xml.getAttrOriginal = xml.getAttr.bind({});
    xml.getTextOriginal = xml.getText.bind({});
    xml.selectAllOriginal = xml.selectAll.bind({});

    /**
     * Override XML.fromString method in the offscreen document.
     */
    xml.fromString = (string) => {
        return string;
    }

    /**
     * Override XML.getAttr method in the offscreen document.
     */
    xml.getAttr = (xmlString, selector, attr) => {
        const callback = (message) => {
            const {errObj, attr} = message;
            if (errObj) {
                throw new Error(errObj.message);
            } else {
                return attr;
            }
        };

        const msgBody =
            {type: events.backend.XML_GET_ATTR, data: {xmlString, selector, attr}};
        return offscreenManager.sendMessage(
            msgBody, 'XML getAttr timed out', callback);
    }

    /**
     * Override XML.getText method in the offscreen document.
     */
    xml.getText = (xmlString, selector) => {
        const callback = (message) => {
            const {errObj, text} = message;
            if (errObj) {
                throw new Error(errObj.message);
            } else {
                return text;
            }
        };

        const msgBody =
            {type: events.backend.XML_GET_TEXT, data: {xmlString, selector}};
        return offscreenManager.sendMessage(
            msgBody, 'XML getText timed out', callback);
    }

    /**
     * Override XML.selectAll method in the offscreen document.
     */
    xml.selectAll = (selector, parent) => {
        const callback = (message) => {
            const {errObj, selection} = message;
            if (errObj) {
                throw new Error(errObj.message);
            } else {
                return selection;
            }
        };

        const msgBody =
            {type: events.backend.XML_SELECT_ALL, data: {selector, parent}};
        return offscreenManager.sendMessage(
            msgBody, 'XML selectAll timed out', callback);
    }
};



