const errorBooster = require('../error-booster');

let creationCount = 0;

class OffscreenManager {
    constructor() {
        this.maybeCloseOffscreen = this.maybeCloseOffscreen.bind(this);
        this.messages = new Map();
    }

    static hasOffscreen() {
        if ('getContexts' in chrome.runtime) {
            return chrome.runtime.getContexts({
                contextTypes: ['OFFSCREEN_DOCUMENT']
            }).then(contexts => !!contexts.length);
        } else {
            return clients.matchAll().then(matchedClients =>
                matchedClients.some(client =>
                    client.url.includes(chrome.runtime.id)));
        }
    }

    _getMessageId() {
        let uniqueId;
        do {
            uniqueId = Math.random().toString(36).substr(2, 9);
        } while (this.messages.has(uniqueId));
        return uniqueId;
    }

    _isTabsLeft() {
        return chrome.tabs.query({}).then((tabs) => tabs.length !== 0);
    }
    
    _closeOffscreen() {
        return OffscreenManager.hasOffscreen().then((hasDocument) => {
            if (hasDocument) {
                return chrome.offscreen.closeDocument();
            }
        }).finally(() => {
            // Can create an offscreen from now on.
            this.isCreatingOffscreenDocument = null;
            
            chrome.tabs.onRemoved.removeListener(this.maybeCloseOffscreen);
        });
    }

    _createOffscreen() {
        return OffscreenManager.hasOffscreen().then((hasDocument) => {
            if (!hasDocument) {
                return chrome.offscreen.createDocument({
                    url: "backend/offscreen.html",
                    reasons: Object.keys(justifications),
                    justification: Object.values(justifications).join('; '),
                }, () => {
                    if (chrome.runtime.lastError) {
                        // Attempt again on next sendMessage.
                        this.isCreatingOffscreenDocument = null;
                        errorBooster.uncaught(chrome.runtime.lastError, {creationCount});
                    } else {
                        creationCount += 1;
                        errorBooster.info("Offscreen is created", {creationCount});
                    }
                });
            }
        }).finally(() => {
            // Can close an offscreen from now on.
            this.isClosingOffscreenDocument = null;

            chrome.tabs.onRemoved.addListener(this.maybeCloseOffscreen);
        });
    }

    /**
     * Creates a port connected to the offscreen.
     */
    _connectPort() {
        this.port = chrome.runtime.connect({name: 'offscreen'});

        this.port.onDisconnect.addListener(() => {
            this.port = null;
            for (const entry of this.messages.values()) {
                if (typeof entry === 'object' && entry.reject) {
                    entry.reject(new Error('Port was disconnected'));
                }
            }
            this.messages.clear();
        });

        this.port.onMessage.addListener((message) => {
            const entry = this.messages.get(message.id);
            if (entry) {
                entry.resolve(message);
                this.messages.delete(message.id);
            }
        });

        return Promise.resolve();
    }

    /**
     * Ensures an offscreen is connected.
     */
    _ensurePortConnected() {
        if (this.port) {
            return Promise.resolve();
        }

        this.maybeCreateOffscreen();
        return this.isCreatingOffscreenDocument.then(() => {
            return this._connectPort();
        });
    }

    /**
     * Adds a callback to handle messages from the offscreen.
     */
    addOffscreenListener(callback) {
        if (this.port) {
            this.port.onMessage.addListener(callback);
        }
    }

    /**
     * Removes a callback to handle messages from the offscreen.
     */
    removeOffscreenListener(callback) {
        if (this.port) {
            this.port.onMessage.removeListener(callback);
        }
    }

    /**
     * Closes an offscreen if it exists.
     */
    maybeCloseOffscreen() {
        if (!this.isClosingOffscreenDocument) {
            this.isClosingOffscreenDocument = this._closeOffscreen();
        }
    }

    /**
     * Creates an offscreen if it do not exists.
     */
    maybeCreateOffscreen() {
        if (!this.isCreatingOffscreenDocument) {
            this.isCreatingOffscreenDocument = this._createOffscreen();
        }
    }

    /**
     * Sends message to the offscreen if it exists.
     */
    sendMessage(msgBody, _rejectMessage, callback, opt_errorHandler) {
        msgBody.id = this._getMessageId();
        return this._ensurePortConnected().then(() => {
            return new Promise((resolve, reject) => {
                this.messages.set(msgBody.id, {resolve, reject});
                this.port.postMessage(msgBody);
            });
        }).then((message) => {
            if (callback) {
                return callback(message);
            }
        }).catch((error) => {
            if (opt_errorHandler) {
                opt_errorHandler(error);
            }
            if (error && error.message) {
                errorBooster.uncaught(error.message);
            }
        });
    }
};

module.exports = new OffscreenManager();



