const about = require('../platform/about');
const buildInfo = require('../platform/build-info');

const ErrorType = {
    UNCAUGHT: '690.2361',
    CLIENT: '690.2362',
};

const ErrorLevel = {
    CRITICAL: 'critical',
    DEBUG: 'debug',
    ERROR: 'error',
    INFO: 'info',
    TRACE: 'trace',
    WARN: 'warn',
};

const Environment = {
    DEBUG: 'development',
    PROD: 'production',
};

const SEND_USAGE_STAT_SETTING = 'settings.sendUsageStat';

class ErrorBooster {
    constructor() {}

    send(url) {
        const isUncaught = url.includes(ErrorType.UNCAUGHT);
        return chrome.storage.local.get([SEND_USAGE_STAT_SETTING],
            (params) => {
                if (params[SEND_USAGE_STAT_SETTING] || isUncaught) {
                    return fetch(url).catch(() => this._fallbackError())
                        .catch(console.error);
                }
            });
    }

    uncaught(message, additional = {}) {
        const vars = this._getVars(message, ErrorLevel.CRITICAL, additional);
        return this.send(this._getUrl(ErrorType.UNCAUGHT, vars));
    }

    error(message, additional = {}) {
        const vars = this._getVars(message, ErrorLevel.ERROR, additional);
        return this.send(this._getUrl(ErrorType.CLIENT, vars));
    }

    info(message, additional = {}) {
        const vars = this._getVars(message, ErrorLevel.INFO, additional);
        return this.send(this._getUrl(ErrorType.CLIENT, vars));
    }

    warn(message, additional = {}) {
        const vars = this._getVars(message, ErrorLevel.WARN, additional);
        return this.send(this._getUrl(ErrorType.CLIENT, vars));
    }

    _fallbackError() {
        return fetch(this._getUrl(
            ErrorType.CLIENT,
            `-project=${buildInfo.getErrorboosterProject()},-msg=ebfallback`));
    }

    _getUrl(path, vars) {
        return new URL(
            `/clck/click/path=${path}/vars=${vars}/*`,
            'https://yandex.ru').toString();
    }

    _getVars(message, level, additional){
        const vars = {
            msg: message,
            level,
            project: buildInfo.getErrorboosterProject(),
            service: about.yasoft,
            timestamp: Date.now(),
            version: buildInfo.getVersion(),
            env: buildInfo.isDebug() ? Environment.DEBUG : Environment.PROD,
            useragent: navigator && navigator.userAgent,
            additional: JSON.stringify(additional),
        };

        return Object.keys(vars)
            .filter(key => vars[key])
            .map(key => `-${key}=${encodeURIComponent(
                ErrorBooster.toWellFormed(vars[key]))}`)
            .join(',');
    }

    static toWellFormed(val) {
        return val.toWellFormed ? val.toWellFormed() : val;
    }
}

module.exports = new ErrorBooster();



