'use strict';

const Filer = require('../../libs/filer');
const events = require('./events');

const DATA_URI_REG_EXP = /^data:/;

class FilerHandler {
    constructor() {
        this.filer = new Filer();
    }

    /**
     * Returns whether url string is data uri.
     * @param {String} url
     * @returns {Boolean}
     * @private
     */
    _isDataUri(url) {
        return typeof url === 'string' &&
            DATA_URI_REG_EXP.test(url);
    }

    /**
     * Returns error handler for the backend message.
     * @param {Function} sendResponse
     * @returns {Function}
     * @private
     */
    _getErrorHandler(sendResponse) {
        return (err) => {
            sendResponse({errObj: {name: err.name, message: err.message}});
        }
    }

    /**
     * Handles Filer.init method call.
     * @param {Object} params
     * @private
     */
    _init({opt_initObj, sendResponse}) {
        this.filer.init(opt_initObj, () => {
            sendResponse({})
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.ls method call.
     * @param {Object} params
     * @private
     */
    _ls({dirEntryOrPath, sendResponse}) {
        this.filer.ls(dirEntryOrPath, (entries) => {
            sendResponse({
                entries: entries.map((entry) =>
                    ({fullPath: entry.fullPath, name: entry.name}))
            });
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.mkdir method call.
     * @param {Object} params
     * @private
     */
    _mkdir({path, opt_exclusive, sendResponse}) {
        this.filer.mkdir(path, opt_exclusive, (entry) => {
            sendResponse({
                entry: {fullPath: entry.fullPath, name: entry.name}
            });
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.open method call.
     * @param {Object} params
     * @private
     */
    _open({entryOrPath, sendResponse}) {
        this.filer.open(entryOrPath, (file) => {
            sendResponse({
                file: Filer.fileToObjectURL(file)
            });
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.pathToFilesystemURL method call.
     * @param {Object} params
     * @private
     */
    _pathToFilesystemURL({path, sendResponse}) {
        this.filer.pathToFilesystemURL(path, (path) => {
            sendResponse({path});
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.create method call.
     * @param {Object} params
     * @private
     */
    _create({path, opt_exclusive, sendResponse}) {
        this.filer.create(path, opt_exclusive, () => {
            sendResponse({});
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.mv method call.
     * @param {Object} params
     * @private
     */
    _mv({src, dest, opt_newName, sendResponse}) {
        this.filer.mv(src, dest, opt_newName, (entry) => {
            sendResponse({
                entry: {fullPath: entry.fullPath, name: entry.name}
            });
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.rm method call.
     * @param {Object} params
     * @private
     */
    _rm({entryOrPath, sendResponse}) {
        this.filer.rm(entryOrPath, () => {
            sendResponse({});
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.cd method call.
     * @param {Object} params
     * @private
     */
    _cd({dirEntryOrPath, sendResponse}) {
        this.filer.cd(dirEntryOrPath, (entry) => {
            sendResponse({
                entry: {fullPath: entry.fullPath, name: entry.name}
            });
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.cp method call.
     * @param {Object} params
     * @private
     */
    _cp({src, dest, opt_newName, sendResponse}) {
        this.filer.cp(src, dest, opt_newName, (entry) => {
            sendResponse({
                entry: {fullPath: entry.fullPath, name: entry.name}
            });
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.write method call.
     * @param {Object} params
     * @private
     */
    _write({entryOrPath, writeData, sendResponse}) {
        if (this._isDataUri(writeData)) {
            writeData = Filer.dataURLToBlob(writeData);
        }

        this.filer.write(entryOrPath, writeData, (entry) => {
            sendResponse({
                entry: {name: entry.name, path: entry.fullPath}
            });
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.df method call.
     * @param {Object} params
     * @private
     */
    _df({sendResponse}) {
        this.filer.df((byteUsed, byteLeft, byteCap) => {
            sendResponse({
                data: {byteUsed, byteLeft, byteCap}
            });
        }, this._getErrorHandler(sendResponse));
    }

    /**
     * Handles Filer.getEntry method call.
     * @param {Object} params
     * @private
     */
    _getEntry({fileName, sendResponse}) {
        this.filer.getEntry(fileName, (entry) => {
            sendResponse({
                entry: entry.toURL()
            });
        }, this._getErrorHandler(sendResponse));
    }

    handle(req, _sender) {
        if (!this.isFilerInitializing) {
            this.isFilerInitializing = new Promise((resolve) => {
                this.filer.init({persistent: true, size: 0}, () => {
                    resolve();
                });
            });
        }

        let handler = null;
        switch (req.type) {
            case events.backend.FILER_CD:
                handler = this._cd;
                break;
            case events.backend.FILER_CP:
                handler = this._cp;
                break;
            case events.backend.FILER_CREATE:
                handler = this._create;
                break;
            case events.backend.FILER_DF:
                handler = this._df;
                break;
            case events.backend.FILER_GET_ENTRY:
                handler = this._getEntry;
                break;
            case events.backend.FILER_INIT:
                handler = this._init;
                break;
            case events.backend.FILER_LS:
                handler = this._ls;
                break;
            case events.backend.FILER_MKDIR:
                handler = this._mkdir;
                break;
            case events.backend.FILER_MV:
                handler = this._mv;
                break;
            case events.backend.FILER_OPEN:
                handler = this._open;
                break;
            case events.backend.FILER_PATH_TO_FILESYSTEM_URL:
                handler = this._pathToFilesystemURL;
                break;
            case events.backend.FILER_RM:
                handler = this._rm;
                break;
            case events.backend.FILER_WRITE:
                handler = this._write;
                break;
            default:
                // Cannot handle.
                return;
        }

        handler = handler.bind(this);
        this.isFilerInitializing.then(() => {
            handler(req.data);  
        });

        // Indicate an async response.
        return true;
    }
};

module.exports = new FilerHandler();



