/*
 *   xml
 *   https://github.yandex-team.ru/desktop/slices-new/blob/master/common/adapter/api/xml.js
 */

/* global Sizzle: true, ActiveXObject: true */

'use strict';

var toArray = require('lodash/lang/toArray');

// for escape/unescape
var rxEscape = /&|  |<|>|\r\n|\n|'/g; // eslint-disable-line no-regex-spaces
var rxUnescape = /&amp;|&nbsp;|&lt;|&gt;|<(br|BR)\s*\/?>|&quot;/g;
var mapEscape = {
    '&': '&amp;',
    '  ': '&nbsp; ',
    '<': '&lt;',
    '>': '&gt;',
    '\n': '<br />',
    '\r\n': '<br />',
    '"': '&quot;'
};
var mapUnescape = {
    '&amp;': '&',
    '&nbsp;': ' ',
    '&lt;': '<',
    '&gt;': '>',
    '<br />': '\n',
    '<br/>': '\n',
    '<br>': '\n',
    '&quot;': '"'
};
var funcReplaceEscape = function (str) {
    return mapEscape[str];
};
var funcReplaceUnescape = function (str) {
    return mapUnescape[str.toLowerCase()];
};

var xml = {
    mapEscape: mapEscape,
    mapUnescape: mapUnescape,
    escape: function (str) {
        return str ? String(str).replace(rxEscape, funcReplaceEscape) : '';
    },
    unescape: function (str) {
        return str ? String(str).replace(rxUnescape, funcReplaceUnescape) : '';
    },
    fromString: (typeof DOMParser === 'function'
        ? function (string) {
            return new DOMParser().parseFromString(string, 'text/xml');
        }
        : function (string) {
            var xmlDocument = new ActiveXObject('Microsoft.XMLDOM');
            xmlDocument.async = false;
            xmlDocument.loadXML(string);
            return xmlDocument;
        }
    ),
    select: (typeof Sizzle === 'function'
        ? function (selector, parent) {
            return Sizzle(selector, parent || window.document)[0] || null;
        }
        : function (selector, parent) {
            return (parent || window.document).querySelector(selector);
        }
    ),
    selectAll: (typeof Sizzle === 'function'
        ? function (selector, parent) {
            return Sizzle(selector, parent || window.document);
        }
        : function (selector, parent) {
            return toArray((parent || window.document).querySelectorAll(selector));
        }
    ),
    getText: function (elem, selector) {
        if (elem && selector) {
            elem = xml.select(selector, elem);
        }
        // vitalets:        elem.firstChild && elem.firstChild.data ?
        //  elem.text, ..     VB-5277
        return elem
            ? (elem.textContent || elem.innerText || (elem.firstChild && elem.firstChild.data) || elem.text || '')
            : '';
    },
    setText: function (elem, text) {
        if (elem) {
            elem.textContent = text;
            elem.innerText = text;
        }
    },
    getAttr: function (elem, selector, attr) {
        if (arguments.length === 2) {
            attr = selector;
            selector = null;
        }
        if (elem && selector) {
            elem = xml.select(selector, elem);
        }
        return elem && elem.getAttribute(attr) || '';
    },
    getAttrs: function (node) {
        return toArray(node.attributes).reduce(function (memo, attr) {
            memo[attr.name] = attr.value;
            return memo;
        }, Object.create(null));
    }
};

module.exports = xml;

require('./overrides_v3/xml').override(xml);



