'use strict';

const colorsHandler = require('./colors-handler');
const filerHandler = require('./filer-handler');
const speechkitHandler = require('./speechkit-handler');
const xmlHandler = require('./xml-handler');

const handlers = [colorsHandler, filerHandler, speechkitHandler, xmlHandler];

class Messaging {
    constructor() {}

    init() {
        this._handleConnection = this._getConnectionHandler().bind(this);
        chrome.runtime.onConnect.addListener(this._handleConnection);
    }

    _getConnectionHandler() {
        return (port) => {
            if (port.name === 'offscreen') { 
                port.onMessage.addListener((req) => {
                    if (req.data) {
                        req.data.sendResponse = (message) => {
                            if (typeof message === 'object') {
                                message.id = req.id;
                            }
                            port.postMessage(message);
                        };
                        for (const handler of handlers) {
                            if (handler.handle(req, port.sender)) {
                                return;
                            }
                        }
                    }
                });
            }
        };
    }
};

module.exports = new Messaging();



