'use strict';

const {speechkit} = require('speechkit').ya;
const {UTTERANCE_SILENCE, API_KEY} = require('../voice/consts');
const events = require('./events');

class SpeechkitHandler {
    constructor() {}

    /**
     * Handles speechkit.start method call.
     * @param {Object} params
     * @private
     */
    _init({sendResponse}) {
        this.streamer = new speechkit.SpeechRecognition();

        this.streamer.start({
            apiKey: API_KEY,
            initCallback: () => sendResponse({}),
            errorCallback: error => {
                sendResponse({
                    type: events.offscreen.SPEECHKIT_ERROR,
                    data: {reason: error.message},
                });
            },
            dataCallback: (text, done) => {
                sendResponse({
                    type: events.offscreen.SPEECHKIT_DATA,
                    data: {text, done},
                });
            },
            utteranceSilence: UTTERANCE_SILENCE,
            particialResults: true,
        });
    }

    /**
     * Handles speechkit.stop method call.
     * @param {Object} params
     * @private
     */
    _stop({sendResponse}) {
        try {
            // Streamer may throw websocket close error.
            if (this.streamer) {
                this.streamer.stop();
            }
        } catch (e) {
            // do nothing
        } finally {
            sendResponse({});
        }
    }


    handle(req, _sender) {
        switch (req.type) {
            case events.backend.SPEECHKIT_INIT:
                this._init(req.data);
                break;
            case events.backend.SPEECHKIT_STOP:
                this._stop(req.data);
                break;
            default:
                // Cannot handle.
                return;
        }

        // Indicate an async response.
        return true;
    }
};

module.exports = new SpeechkitHandler();



